import React from 'react';
import PropTypes from 'prop-types';
import FormControl from '@material-ui/core/FormControl';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import "../../assets/css/app.css";

const styles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    fontSize:12
  },
  Listpaper: {
    padding: theme.spacing(2),
    borderRadius : 0,
    color: theme.palette.text.secondary,
    maxHeight: '80vh', overflow: 'auto'
  },
  Expansionpanel: {
    padding: theme.spacing(2),
    borderRadius : 0,
    color: theme.palette.text.secondary,
    maxHeight: '80vh', overflow: 'auto'
  },
  paper: {
    padding: theme.spacing(2),
    borderRadius : 0,
    color: theme.palette.text.secondary,
  },
  nonpaper: {
    boxShadow:'none',
    padding: theme.spacing(2),
  },
  stepperContent:{
    boxShadow: 'none'
  }
}));

export let CreateGridContent = (props, classes) => {
  let contents = [];
    
  contents = props.element.map(function(val, i){

    const tag = React.createElement(val.element, (val.props ? val.props : null), (val.children ? val.children : null));
    
    let pre_tag = null;
  
    if(val.preElement) {
      pre_tag = React.createElement(val.preElement.element, (val.preElement.props ? val.preElement.props : null), (val.preElement.children ? val.preElement.children : null));
    }

    let elem = (
      <FormControl fullWidth key={'ele_' + i}>
          {pre_tag}{tag}
      </FormControl>
    );
    if (val.visible !== 'false') {
      return {'size':val.size ? val.size : props.size, content:elem, visible:val.visible === 'false' ? false : true };
    } else {
      return {'size':false, content:null, visible:val.visible === 'false' ? false : true };
    }
  })
  return contents;

}

export let CustomGridComponent = (props) => {

  const classes = styles();

  return (
    <div className={classes.root}>
      <Grid container spacing={props.gridcontent.spacing}>
        {!props.gridcontent.outergrid ?
          props.gridcontent.grids.map(function (grid,i) {
            return (
              <Grid item sm={grid.size} key={i}>
                {grid.content}
              </Grid>
            )
          }) 
          :
          <Grid item sm={props.gridcontent.outergridsize}>
            <Paper className={props.gridcontent.className ? classes[props.gridcontent.className]  : classes.paper}>
              <Grid container spacing={props.gridcontent.spacing}>
                {props.gridcontent.grids.map(function (grid,i) {
                  return (
                    <Grid item sm={grid.size} key={i}>
                      {grid.content}
                    </Grid>
                  )
                })} 
              </Grid>
            </Paper>
          </Grid>
        }
      </Grid>
    </div>
  );
}