import React from "react";
import { withStyles } from "@material-ui/core/styles";
import PropTypes from "prop-types";
import { Button, Paper } from "@material-ui/core";
import Switch from "@material-ui/core/Switch";
import _ from "underscore";
import Collapse from "@material-ui/core/Collapse";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Divider from '@material-ui/core/Divider';
import Tooltip from "@material-ui/core/Tooltip";
import {Search, CloudDownload} from "@material-ui/icons";
import TextField from "@material-ui/core/TextField";
import Typography from '@material-ui/core/Typography';
import Grid from "@material-ui/core/Grid";
import HeadingComponent from "../generic/HeadingComponent";
import EmptyDataComponent from "../generic/DataEmptyPage";
import ViewComponent from "../generic/ViewComponent";
import ReactJson from 'react-json-view'
import { GetSelectOptions } from "../../utils/select/SelectComponent";
import { marketplaces, headingText, noDataText,defaultKeywordDataText } from "../../constants/constant.js";
import Api from "../../api/search/SearchService.js";
import "../../assets/css/app.css";
import moment from "moment";

const styles = theme => ({
  root: {
    display: "flex",
    flexWrap: "wrap"
  },
  margin: {
    margin: theme.spacing.unit
  },
  ModuleHeading: {
    color: "#696969",
    fontWeight: 600
  },
  withoutLabel: {
    marginTop: theme.spacing.unit * 3
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    fontSize: 12
  },
  textFieldLabel: {
    fontSize: 16
  },
  tooltip: {
    fontSize: 10,
    margin: theme.spacing.unit
  },
  button: {
    marginTop: theme.spacing.unit * 2,
    fontSize : 11,
    fontWeight : 'bold'
    // backgroundColor:'#E30047'
  },
  textFieldInput : {
    fontSize: 12
  },
  leftIcon: {
    marginRight: theme.spacing.unit,
    fontSize: 20
  },
  rightIcon: {
    marginLeft: theme.spacing.unit,
    fontSize: 20
  },
  container: {
    display: "flex",
    flexWrap: "wrap"
  }
});


class SearchComponent extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          search: true,
          page : 0,
          keyword : {
            search_text : '',
            marketplace : 'Redsky Target'
          },
          productdata : null,
          emptyTextContainerData: "",
          displayDataTypes : false
        };

        this.searchProduct = this.searchKeyword.bind(this);
        this.handleValueChange = this.handleValueChange.bind(this);
        this.disableButton = this.disableButton.bind(this);
        this.getNextPrevRecords = this.getNextPrevRecords.bind(this);

    }

    componentDidMount = () => {
      this.setState({
        emptyTextContainerData : defaultKeywordDataText
      })
    }

    searchKeyword = async (p) => {

      const {keyword} = this.state;

      let marketplace = _.filter(marketplaces, function(m){return m.label == keyword.marketplace});

      let params = {
        url : marketplace[0].url
      }

      let data = {
        'id' : keyword.search_text
      }
      
      params['data'] = data;

      console.log(params)

      let response = await Api._SearchKeyword(params);

      let res_data = response && response.data ? response.data : null;

      this.setState({productdata : res_data})

    }

    handleValueChange = event => {
      const keyword = this.state.keyword;
      keyword[event.target.name] = event.target.value;
      this.setState({ keyword: keyword });
    };

    disableButton = props => {
        if (!this.state.keyword.search_text) {
            return true
        } else {
            return false
        }
    }

    getNextPrevRecords = (page) => {
      this.setState({
        page : page
      })
      this.searchProduct(page)
    }

    render() {

        const { classes } = this.props;
        const { emptyTextContainerData }  = this.state;

        const FilterContentJson = {
          size: 2,
          visible: true,
          element: [
            {
                props: {
                  label: "Keyword",
                  className: classes.textField,
                  placeholder: "Type any keyword here..",
                  margin: "normal",
                  variant : 'outlined',
                  name: "search_text",
                  size : "small",
                  value: this.state.keyword.search_text,
                  onChange: this.handleValueChange,
                  type: "text",
                  inputProps:{
                      className : classes.textFieldInput,
                  },

                },
                size: 8,
                element: TextField
            },
            {
              props: {
                label: "Marketplace",
                className: classes.textField,
                placeholder: "Select marketplace",
                margin: "normal",
                select: marketplaces ? true : false,
                name: "marketplace",
                variant : 'outlined',
                value: this.state.keyword.marketplace,
                onChange: this.handleValueChange,
                type: "text",
                size : "small",
                inputProps:{
                  className : classes.textFieldInput,
                },
                SelectProps: {
                  native: true,
                  MenuProps: {
                    className: classes.menu
                  }
                }
              },
              children: GetSelectOptions(marketplaces),
              size: 2,
              element: TextField
            },
            {
              element: Tooltip,
              size: 2,
              props: {
                title: "Search Keyword",
                placement: "top",
                classes: { tooltip: classes.tooltip }
              },
              children: (
                <Button
                  variant="contained"
                  size="medium"
                  color="secondary"
                  aria-label="Search"
                  onClick={e => this.searchKeyword(this.state.page)}
                  className={classes.button}
                  disabled={this.disableButton()}
                >
                  <Search className={classes.leftIcon} /> Search{" "}
                </Button>
              )
            }
          ]
        };

        return (
            <div className = "MainContainer"> 
              <div className="SearchContainer">
                  <Grid container spacing={2}>
                      <Grid item sm={12}>
                        <HeadingComponent heading={headingText.search.title} variant={"h5"} style={classes.ModuleHeading} />
                        <Divider style={{ border: ".5px solid", color:'#5179c5'}}/>
                        <ViewComponent contentjson={FilterContentJson}/>
                        {this.state.productdata? (
                          <div
                            className="KeywordTableContainer"
                            style={{ margin: "10px auto", padding: "10px" }}
                          >
                            <Paper className={classes.paper} style={{ margin: "10px auto", padding: "10px" }}>
                              <ReactJson src={this.state.productdata} displayDataTypes={false} />
                            </Paper>
                          </div>
                        ) : (
                          <EmptyDataComponent data={emptyTextContainerData} paper={true} />
                        )}
                      </Grid>
                  </Grid>  
              </div>
            </div>
        );
    }
}

SearchComponent.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(SearchComponent);
