import React from "react";
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import { withStyles } from "@material-ui/core/styles";
import PropTypes from "prop-types";
import "../../assets/css/app.css";

const styles = theme => ({
  paper: {
    padding: theme.spacing.unit * 2,
    borderRadius : 0,
    color: theme.palette.text.secondary,
  },
  typography: {
    color:'#757277'
  }
});

class EmptyDataComponent extends React.Component {

  render() {

    const { classes, data, paper } = this.props;

    return (
      <div className = 'dataContainer'>
        {paper ? 
          <Paper className={classes.paper}>
            <div className="dataEmptyContainer">
              <Typography disabletypography = "true" variant="h6" className={classes.typography} gutterBottom>
                  {data}
              </Typography>  
            </div>
          </Paper> :
          <div className="dataEmptyContainer">
            <Typography disabletypography = "true" variant="h6" className={classes.typography} gutterBottom>
                {data}
            </Typography>  
          </div>
        }
      </div>
    );
  }
}

EmptyDataComponent.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(EmptyDataComponent);