import React from "react";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";

class HeadingComponent extends React.Component {
  render() {
    const { heading, variant, style, hr, gutterBottom, headingclass } = this.props;

    const styleCss = headingclass ? headingclass : { margin: "10px auto" };

    return (
      <div
        className="ModuleHeadingContainer"
        style={styleCss}
      >
        <Grid container spacing={2}>
          <Grid item sm={8}>
            <Typography
              variant={variant}
              className={style}
              gutterBottom={gutterBottom === "false" ? false : true}
              disabletypography="true"
            >
              {heading}
            </Typography>
            {hr ? <Divider variant="fullWidth" /> : null}
          </Grid>
        </Grid>
      </div>
    );
  }
}

export default HeadingComponent;
