import React from "react";
import { withStyles } from "@material-ui/core/styles";
import PropTypes from "prop-types";
import {CustomGridComponent,CreateGridContent} from "./GridComponent";

const styles = theme => ({
  root: {
    display: "flex",
    flexWrap: "wrap"
  },
  margin: {
    margin: theme.spacing.unit
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    fontSize: 12
  },
  tooltip: {
    fontSize: 10,
    margin: theme.spacing.unit
  },
  button: {
    margin: theme.spacing.unit
  },
  container: {
    display: "flex",
    flexWrap: "wrap"
  }
});

class ViewContentComponent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    const { classes, contentjson, isCollapse } = this.props;

    let grid_data = CreateGridContent(contentjson, classes);

    let gridData = {
      outergrid: false,
      spacing: 2,
      outergridsize: 12,
      grids: grid_data
    };

    return (
      <div
        className="FilterContainer"
        style={{ margin: "10px auto"}}
      >
        <CustomGridComponent gridcontent={gridData} />
      </div>
    );
  }
}

ViewContentComponent.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(ViewContentComponent);
